body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  margin: 0;
  background-color: #222;
}
header,
main,
button {
  display: block;
}
a {
  text-decoration: none;
}
i {
  color: #555;
}
button:hover i {
  color: #999;
}
h1 {
  margin: 0;
}
.wrapper {
  color: #999;
  font-family: aktiv-grotesk-std, Helvetica Neue, Arial, sans-serif;
}
.header {
  padding: 10px 0;
  height: 40px;
}
.c-row {
  margin: 0 auto;
  width: 100%;
  max-width: 810px;
}
.c-col {
  float: left;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
}
.header-title {
  float: left;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
  line-height: 40px;
}
.header-title:before {
  display: inline-block;
  border: 2px solid #eee;
  margin-right: 8px;
  border-radius: 100%;
  height: 12px;
  width: 12px;
  content: " ";
}
.header-action {
  float: right;
  padding: 8px 0;
  display: flex;
}
.header-action .btn-c {
  width: 100%;
  background-color: transparent;
  border: 1px solid transparent;
  text-align: center;
  margin-bottom: 0;
  color: #999;
  font-size: 14px;
  padding: 0;
  padding-right: 12px;
}
.sign-out-btn {
  border-right: 1px solid #333;
  padding-top: 3px;
}
.icon-link {
  padding-left: 12px;
}
.sign-in {
  max-width: 300px;
  margin-top: 90px;
}
.sign-in-heading {
  text-align: center;
  font-size: 30px;
  font-weight: 300;
}
.sign-in-button {
  margin-top: 30px;
}
.btn-c {
  width: 100%;
  background-color: transparent;
  border: 1px solid transparent;
  text-align: center;
  border: 1px solid #555;
  margin-bottom: 10px;
  padding: 15px 0;
  color: #999;
  font-size: 18px;
  cursor: pointer;
}
form {
  width: 100%;
}
.form-c {
  margin-top: 90px;
}
.input-c {
  border: 0;
  border-bottom: 1px dotted #666;
  background: transparent;
  height: 60px;
  font-size: 32px;
  width: 100%;
  outline: none;
  color: white;
}
.tasks {
  list-style: none;
  display: flex;
  padding-left: 0;
}
.tasks li:not(:first-child)::before {
  padding-right: 12px;
  content: "/";
  font-weight: 300;
  padding-left: 12px;
}

.cell {
  display: flex;
  align-items: center;
  border-bottom: 1px dotted #666;
}
.cell-item {
  flex: 1;
}
.btn-icon {
  border-radius: 40px;
  padding: 8px;
  width: 40px;
  height: 40px;
  margin-bottom: 0;
  background: #2a2a2a;
  border: 0;
}
.item-text {
  padding-left: 10px;
  font-size: 24px;
  color: #fff;
  font-weight: 300;
  margin: 15px 0;
}
.task-item a {
  cursor: pointer;
}
.btn-active {
  color: #85bf6b;
}
.text-active {
  text-decoration: line-through #85bf6b;
  color: #666;
}
.input-item {
  border: 0;
  background: transparent;
  width: 100%;
  outline: none;
}
.active {
  color: white;
}
.sign-in-button .btn-c:hover {
  border: 2px solid #aaa;
  cursor: pointer;
}
.loader {
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* align-content: center; */
}
.color-btn {
  background: transparent;
  border: 0;
  cursor: pointer;
  outline: none;
}
@media only screen and (max-width: 576px) {
  .input-c {
    font-size: 24px;
  }
}
.black{
  backgroundColor: black,
}
.white{
  backgroundColor: white,
}